.critter-icon {
  position: relative;
  border-radius: 25%;
  width: 250px;
  height: 250px;
  overflow: hidden;
}

.critter-icon__critter {
  z-index: 1;
  width: 46%;
  position: absolute;
  top: 11%;
  left: 7%;
  transform: rotate(-15deg) scaleX(100%);
}

.critter-icon__critter--center {
  scale: 1.5;
  top: 26%;
  left: 26%;
}

.critter-icon:hover * .critter-icon__critter,
.critter-icon:hover > .critter-icon__critter {
  animation: wings-flapping 1.75s infinite;
}

.critter-icon__fish {
  z-index: 1;
  width: 52%;
  position: absolute;
  bottom: 14%;
  left: 27%;
}

.critter-icon__fish--center {
  width: 75%;
  bottom: 29%;
  left: 12%;
}

.critter-icon:hover * .critter-icon__fish,
.critter-icon:hover > .critter-icon__fish {
  animation: fish-bouncing 2s infinite;
}

.critter-icon__sea-creature {
  transform: rotate(15deg) scaleX(100%);
  z-index: 1;
  width: 35%;
  position: absolute;
  top: 7%;
  right: 9%;
}

.critter-icon__sea-creature--center {
  scale: 1.5;
  top: 25%;
  right: 32%;
}

.critter-icon:hover > *.critter-icon__sea-creature,
.critter-icon:hover > .critter-icon__sea-creature {
  animation: sea-creature-swimming 1.5s infinite;
}

.critter-icon__background {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  background: #f6cd64;
  width: 100%;
  height: 100%;
}

.critter-icon:hover * .critter-icon__ripple {
  border-radius: 100%;
  width: 125%;
  height: 125%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.critter-icon:hover * .critter-icon__ripple--1 {
  background: #fcba14;
  animation: ripple-1 4s infinite forwards cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.critter-icon:hover * .critter-icon__ripple--2 {
  background: #f6cd64;
  animation: ripple-2 4s infinite forwards cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.critter-icon--solo:hover * .critter-icon__ripple {
  border-radius: 100%;
  width: 125%;
  height: 125%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  background-image: radial-gradient(#caa64a 20%, transparent 20%),
    radial-gradient(#f5d992 20%, transparent 20%);
  background-color: #fcba14;
  background-position: 0 0, 25px 25px;
  background-size: 50px 50px;
  animation: random-polka 5s infinite;
}

@keyframes random-polka {
  0% {
    background-position: 0 0, 25px 25px;
  }
  50% {
    background-position: 150px 150px, 100px 100px;
  }
  100% {
    background-position: 0 0, 25px 25px;
  }
}

@keyframes wings-flapping {
  0% {
    transform: rotate(-15deg) scaleX(100%);
  }

  8% {
    transform: rotate(-15deg) scaleX(90%);
  }

  16% {
    transform: rotate(-15deg) scaleX(100%) translateY(2%);
  }

  24% {
    transform: rotate(-15deg) scaleX(90%) translateY(2%);
  }

  32% {
    transform: rotate(-15deg) scaleX(100%) translateY(4%);
  }

  40% {
    transform: rotate(-15deg) scaleX(90%) translateY(4%);
  }

  48% {
    transform: rotate(-15deg) scaleX(100%) translateY(6%);
  }

  56% {
    transform: rotate(-15deg) scaleX(90%) translateY(6%);
  }

  64% {
    transform: rotate(-15deg) scaleX(100%) translateY(6%);
  }

  80% {
    transform: rotate(-15deg) scaleX(90%) translateY(2%);
  }

  100% {
    transform: rotate(-15deg) scaleX(100%);
  }
}

@keyframes fish-bouncing {
  50% {
    transform-origin: right bottom;
    transform: translateY(15%) rotate(-12deg);
  }

  100% {
    transform-origin: right bottom;
    transform: translateY(0) rotateY(0deg);
  }
}

@keyframes sea-creature-swimming {
  0% {
    transform: rotate(15deg) scaleX(100%);
  }

  16% {
    transform: rotate(15deg) scaleX(90%) translateY(3%);
  }

  32% {
    transform: rotate(15deg) scaleX(100%) translateY(6%);
  }

  48% {
    transform: rotate(15deg) scaleX(90%) translateY(10%);
  }

  64% {
    transform: rotate(15deg) scaleX(100%) translateY(6%);
  }

  80% {
    transform: rotate(15deg) scaleX(90%) translateY(3%);
  }

  100% {
    transform: rotate(15deg) scaleX(100%);
  }
}

@keyframes ripple-1 {
  0% {
    transform: translate(-50%, -50%) scale(0.1);
    z-index: 2;
  }

  50% {
    transform: translate(-50%, -50%) scale(1);
    z-index: 2;
  }

  99.98% {
    transform: translate(-50%, -50%) scale(1);
    z-index: 2;
  }

  95% {
    transform: translate(-50%, -50%) scale(1);
    z-index: 1;
  }

  100% {
    transform: translate(-50%, -50%) scale(0.1);
    z-index: 2;
  }
}

@keyframes ripple-2 {
  0% {
    z-index: 1;
    transform: translate(-50%, -50%) scale(1);
  }

  49.99% {
    transform: translate(-50%, -50%) scale(1);
    z-index: 1;
  }

  50% {
    transform: translate(-50%, -50%) scale(0.1);
    z-index: 2;
  }

  99.99% {
    transform: translate(-50%, -50%) scale(1);
    z-index: 2;
  }

  100% {
    z-index: 1;
  }
}
